<template>
  <div :class='getDarkMode ? "dark-mode-goBack" : ""' class="goBack">
    <router-link to="/" class="link">
      <i class="fas fa-arrow-left"></i>
      <span>Back</span>
    </router-link>
  </div>
  <div v-if="loaded" :class='getDarkMode ? "dark-mode-card" : ""'>
  <div class="country" v-for="country in getSingleCountryDetails" :key="country" :class='getDarkMode ? "dark-mode-card" : ""'>
      <div class="flag">
        <img :src="country.flags.svg" alt="flag" />
      </div>
      <div class="country-info">
        <h2 class="country-name">{{ country.name }}</h2>
        <!-- <p class="country-native-name">
          <span>Native Name:</span> {{ country.name.nativeName.ara.common }}
        </p> -->
        <p class="country-population">
          <span>Population:</span> {{ country.population  }}
        </p>
        <p class="country-region">
          <span>Region:</span> {{ country.region }}
        </p>
        <p class="country-sub-region">
          <span>Sub Region:</span> {{ country.subregion }}
        </p>
        <p class="country-capital">
          <span>Capital:</span> {{ country.capital }}
        </p>
        <p class="country-top-level-domain">
          <span>Top Level Domain:</span> {{ country.topLevelDomain.join(', ') }}
        </p>
        <p class="country-currencies">
          <span>Currencies:</span> {{ country.currencies.map((currency) => currency.name).join(', ') }}
        </p>
        <p class="country-languages">
          <span>Languages:</span> {{ country.languages.map((language) => language.name).join(', ') }}
        </p>
        <p class="country-border-countries" v-if="country.borders">
          <span>Border Countries:</span>
          <button v-for="border in country.borders" :key="border">
            {{ border }}</button>
        </p>
        <p class="country-border-countries" v-if="!country.borders">
          <span>Border Countries:</span>
          N/A
        </p>

      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { mapGetters,mapActions } from 'vuex';

export default {
  name: 'CardDetails',
  data: () => ({
    Country: {},
    loaded: false,
    languages: [],
    currencies: [],
  }),
  methods: {
    ...mapActions(['getCountryByName']),
  
  },
  computed: {
    ...mapGetters(['getSingleCountryDetails','getDarkMode']),
  },
  mounted() {
    this.getCountryByName(this.$route.query.name);
    this.loaded = true;
  },

  

}
</script>

<style scoped>
.country {
  margin-top: 2rem;   
}

.flag img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    
  }

.country-info {
  margin: 0 0 0 1rem;
  padding: 1rem;
  
}

.country-info h2 {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.country-info p {
  font-size: 1rem;
  font-weight: 400;
  margin-bottom: 0.5rem;
}

.country-info span {
  font-size: 1rem;
    font-weight: 800;
    margin-bottom: 0.5rem;
}

.country-top-level-domain {
  margin-top: 3rem;
}

.country-border-countries button {
  margin: .5rem;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 5px;
  background: hsl(0, 0%, 90%);
  color: hsl(200, 15%, 8%);
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.country-border-countries button:hover {
  background: hsl(0, 0%, 80%);
  color: hsl(0, 0%, 100%);
}

.link {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
  text-decoration: none;
  color: hsl(200, 15%, 8%);
  font-size: 1.3rem;
  font-weight: 600;
  transition: all 0.3s ease-in-out;
  padding: 1.5rem;
  width: 14%;
  border-radius: 5px;
  margin-top: 1rem;
  border: 2px solid hsl(0, 0%, 90%);
  padding: 1rem;
}

.link:hover {
  background: hsl(0, 0%, 90%);
}

.link i {
  margin-right: 0.5rem;
}

.dark-mode-goBack {
  background: hsl(209, 23%, 22%);
  color: hsl(0, 0%, 100%);
  border: 2px solid hsl(209, 23%, 22%);
  margin-top: -0.2rem;

}

.dark-mode-goBack:hover {
  background: hsl(209, 23%, 22%);
  color: hsl(0, 0%, 100%);
  border: 2px solid hsl(209, 23%, 22%);
}

.dark-mode-goBack span {
  color: hsl(0, 0%, 100%);
}

.link {
    width: 15%;
    margin: 0 auto;
  }

  .fas {
    color: white;
  }

@media (min-width: 768px) {
  .country {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 5rem;
  }

  .flag {
    width: 50%;
    height: 400px;
  }

  .flag img {
    width: 90%;
    height: 100%;
    object-fit: cover;
  }

  .country-info {
    width: 50%;
    margin: 0;
    padding: 0;
  }
}

@media screen and (max-width: 500px) {
  .link {
    width: 40%;
  }

}




</style>