<template>
  <form class="box">
    <v-text-field
            label="Search for a country..."
            prepend-inner-icon="mdi-magnify"
            v-model="search"
            @keypress.enter="check"
            bg-color="white"
          ></v-text-field>
   </form>
    <div v-if="getCountryNotFound" class="box">
      <p :class="getDarkMode ? 'dark-mode-error' : ''">Sorry, no results found</p>
    </div>

</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'SearchInput',
  data: () => ({
    search: '',
  }),
  methods: {
    ...mapActions(['searchCountry']),
    check(e) {
      e.preventDefault();
      this.searchCountry(this.search);
      this.search = '';
    },
  },
  computed: {
    ...mapGetters(['getCountryNotFound','getDarkMode']),
  },
}
</script>

<style scoped>
.box {
  margin : 1rem auto 0 0; 
  width: 30%;
  padding: 1rem;
}

.box p {
  color: black;
  font-size: 1.2rem;
}

.box p.dark-mode-error {
  color: white;
}

@media screen and (max-width: 500px) {
  .box {
    width: 100%;
  }
}

</style>