<template>
  <div :class='getDarkMode ? "wrapper" : ""'>
  <SearchInput />
  <div class="filter-cont">
    <v-select
     label="Filter by Region"
     :items="['Africa', 'America', 'Asia', 'Europe', 'Oceania']"
     v-model="selected"
     bg-color="white"
    ></v-select>
  </div>
  <div class="countries-cont">
    <div @click="test(country.name)" class="country"
    :class='getDarkMode ? "dark-mode-card" : ""' v-for="country in getCountries" :key="country.area">
      <!-- <router-link :to="country.name.common" class="link"> -->
      <div class="flag">
        <img :src="country.flags.svg" alt="flag" />
      </div>
      <div class="country-info">
        <h2 class="country-name">{{ country.name }}</h2>
        <p class="country-population">
          <span>Population:</span> {{ country.population }}
        </p>
        <p class="country-region">
          <span>Region:</span> {{ country.region }}
        </p>
        <p class="country-capital">
          <span>Capital:</span> {{ country.capital }}
        </p>
      </div>
    <!-- </router-link> -->
    </div>
  </div>
</div>
</template>

<script>
import SearchInput from './SearchInput.vue'
import { mapActions, mapGetters } from 'vuex';
import CardDetails from './CardDetails.vue';

export default {
  name: 'Card',
  components: {
    SearchInput,
    CardDetails,
  },
  data: () => ({
    selected: null,
  }),
  methods: {
    ...mapActions(['fetchAllCountries','filterByRegion',]),
    test(name) {
      this.$router.push({ path: `/Countries`, query: { name: `${name}`} });

    },
  },
  computed: {
    ...mapGetters(['getCountries','getDarkMode']),
  },
  mounted() {
    this.fetchAllCountries();
  },
  watch: {
    selected() {
      this.filterByRegion(this.selected);
    },
  },
}
</script>

