import axios from 'axios'
import { createStore } from 'vuex'

export default createStore({
  state: {
    Countries: [],
    SingleCountryDetails: {},
    CountryNotFound: false,
    darkMode: false
  },
  getters: {
    getCountries(state) {
      return state.Countries
    },
    getSingleCountryDetails(state) {
      return state.SingleCountryDetails
    },
    getCountryNotFound(state) {
      return state.CountryNotFound
    },
    getDarkMode(state) {
      return state.darkMode
    }
  },
  mutations: {
    setCountries(state, payload) {
      state.Countries = payload
    },
    setSingleCountryDetails(state, payload) {
      state.SingleCountryDetails = payload
    },
    setCountryNotFound(state, payload) {
      state.CountryNotFound = payload
    },
    setDarkMode(state, payload) {
      state.darkMode = payload
    }

  },
  actions: {
    async fetchAllCountries({ commit }) {
      const response = await axios.get('https://restcountries.com/v2/all')
      const data = await response.data
      commit('setCountries', data)
    },
    async getCountryByName({ commit }, name) {
      const response = await axios.get(`https://restcountries.com/v2/name/${name}`)
      const data = await response.data
      console.log(data)
      commit('setSingleCountryDetails', data)
    },
    async filterByRegion({ commit }, region) {
      commit('setCountries', [])
      const response = await fetch(`https://restcountries.com/v2/region/${region}`)
      const data = await response.json()
      commit('setCountries', data)
    },
    async searchCountry({ commit }, name) {
      fetch(`https://restcountries.com/v2/name/${name}`)
        .then(response => response.json())
        .then(data => {
          if (data.status === 404) {
            commit('setCountryNotFound', true)
          } else {
            commit('setCountryNotFound', false)
            commit('setCountries', data)
          }
        }).catch(err => {
          console.error("something went wrong")
        })
    },


  },
  modules: {
  }
})
