<template>
  <header :class='getDarkMode ? "dark-mode" : ""'>
    <div class="title">
      <h1>Where in the world?</h1>
    </div>
    <div @click="changeTheme" class="theme" v-if="!getDarkMode">
      <i class="fas fa-moon"></i>
      <span>Dark Mode</span>
    </div>
  
    <div @click="changeTheme" class="theme" v-if="getDarkMode">
      <i class="fas fa-sun"></i>
      <span>Light Mode</span>
    </div>
  </header>
  <hr>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'Header',
  data: () => ({
    //
  }),
  computed: {
    ...mapGetters(['getDarkMode']),
  },
  methods: {
    changeTheme() {
      this.$store.commit('setDarkMode', !this.getDarkMode);
    },
  },
}
</script>

<style scoped>
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
}

.theme {
  display: flex;
  align-items: center;
  gap: .5rem;
  cursor: pointer;
  padding: 1rem;
  margin-right: 2rem;
}

.theme:hover {
  background: hsl(0, 0%, 90%);
}

.theme span {
  font-size: 1rem;
  font-weight: 600;
}
.fa-moon {
  font-size: 1.5rem;
}
</style>